import React from 'react';
import { Form, FormikProvider } from 'formik';
import { useAsyncFn } from 'react-use';
import { isEmpty } from 'lodash';

import { __t, SubscriptionFormBlockKind } from '@mssgme/shared';
import { useEventCallback } from '@mssgme/hooks';
import { ApiError, ValidationError } from '@mssgme/api-client';
import { LandingsPropTypes, useEnhancedFormik } from '@mssgme/ui';
import { DynamicFormInputs, useDynamicForm } from '../../UI';
import { useBlockTheme, useCRMData, useFrontAPI } from '../../hooks';
import { PaymentStep } from './PaymentStep';

const applyError = (err, helpers) => {
    if (!ValidationError.is(err)) {
        return;
    }

    for (const [fieldName, message] in Object.entries(err.errors)) {
        helpers.setFieldError(fieldName, __t(message));
    }
};

CheckoutForm.propTypes = {
    order: LandingsPropTypes.order.isRequired,
};

export function CheckoutForm({ order }) {
    const orderId = order._id;
    const api = useFrontAPI();
    const { theme } = useBlockTheme({ kind: SubscriptionFormBlockKind });
    const { form } = useCRMData();
    const { validationSchema, initialValues, mapValues } = useDynamicForm({ items: form.fields });

    const [{ loading }, onSubmit] = useAsyncFn(
        async (fields, helpers) => {
            try {
                await api.orders.update({ orderId, payload: { fields } });
            } catch (err) {
                applyError(err, helpers);

                throw err;
            }
        },
        [orderId, mapValues]
    );

    const disabled = order.finalized || loading || !form.fields.length;

    const formik = useEnhancedFormik({
        initialValues,
        validationSchema,
        onSubmit,
    });

    const handleAccept = useEventCallback(async () => {
        const valid = isEmpty(await formik.validateForm());

        try {
            await formik.submitForm();

            // useEnhancedFormik swallows the error, thrown in onSubmit and treats it as 'result'.
            // Apply it manually here
            if (ApiError.is(formik.submit.result)) {
                applyError(formik.submit.result, formik);

                return false;
            }
        } catch (e) {
            return false;
        }

        return valid;
    });

    return (
        <div>
            <FormikProvider value={formik}>
                <Form>
                    <DynamicFormInputs items={form.fields} theme={theme} />
                </Form>
            </FormikProvider>

            <PaymentStep order={order} disabled={disabled} onAccept={handleAccept} />
        </div>
    );
}
